.portfolioWrapper {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  display: grid;
  grid-template-columns: 50% 50%;
}

.portfolioImage {
  width: 100%;
  overflow: hidden;
  margin-bottom: 24px;
}

.portfolioImage img {
  width: 100%;
}

.portfolioImageLarge img {
  width: 100%;
  overflow: hidden;
}

@media screen and (max-width: 820px) {
  .portfolioWrapper {
    grid-template-columns: 100%;
    grid-template-rows: auto;
    gap: 25px;
    text-align: center;
  }
}
