@import url("https://fonts.googleapis.com/css2?family=Italianno&family=Bricolage+Grotesque:opsz,wght@10..48,500&family=Lavishly+Yours&family=Monoton&family=Poiret+One&family=Tangerine:wght@400;700&display=swap");

* {
  box-sizing: border-box;
}
.page {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.section {
  box-sizing: border-box;
  width: 100%;
  padding: 40px 200px;
  /* margin: 40px 250px; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  background-color: black;
  color: white;
  border: none;
  padding: 12px 24px;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 2px;
}

.button:hover {
  background-color: grey;
}

h5 {
  font-family: "Italianno", cursive;
  font-family: "Tangerine", cursive;
  font-family: "Monoton", cursive;
  font-family: "Lavishly Yours", cursive;
  font-size: 36px;
  font-weight: 200;
  margin: 0 0 12px 0;
}

h4 {
  font-family: "Poiret One", cursive;
  font-size: 14px;
  margin: 0 0 24px 0;
  text-transform: uppercase;
  letter-spacing: 5px;
}

h3 {
  font-family: "Bricolage Grotesque", sans-serif;
  font-size: 25px;
  margin: 0 0 16px 0;
}

p,
i {
  font-size: 14px;
  line-height: 18px;
}

.image {
  /* background-color: #cccccc; */
  width: 120px;
  height: 170px;
  overflow: hidden;
}

@media screen and (max-width: 820px) {
  .section {
    margin: 0;
    padding: 0 25px;
  }
}
