.clientsWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f7f3e9;
  padding: 25px 45px;
}

.clientsImageWrapper {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin: 15px 0;
}

@media screen and (max-width: 820px) {
  .clientsWrapper {
    width: 100%;
  }
}
