.footer {
  background-color: #060606;
  min-height: 40vh;
  margin-top: 45px;
  padding: 25px 20px;
  color: #ffffff;
}

.phone {
  text-decoration: none;
}

.link {
  font-weight: bold;
  text-decoration: none;
}

.phone:hover,
.link:hover {
  text-decoration: underline;
}

.details {
  margin: 50px 0 0 0;
}

@media screen and (max-width: 820px) {
  .footer {
    margin-top: 25px;
  }
}
