.instagramWrapper {
  background-color: #f7f3e9;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 40px 250px;
}

@media screen and (max-width: 820px) {
  .instagramWrapper {
    padding: 40px 0;
    text-align: center;
  }

  .instagramWrapper h4 {
    margin: 0;
    padding: 0;
  }
}
